/* Home stylesheet */
/* Version 1.2 */

/* Color Theme - #D90D58 - Wine Pink, #1EB7D9 - Bright Cerulean*/

/* =============================================================*/
/* ==================== Basic configuration ====================*/
/* =============================================================*/

@import "./style.css";
@import url('https://fonts.googleapis.com/css2?family=Gideon+Roman&family=Roboto&display=swap');

h1, .index h3, .contact h3 {
    font-family: Helvetica, sans-serif;
}

/* =============================================================*/
/* ======================= Main Contents =======================*/
/* =============================================================*/

.main {
    position: absolute;
    width: 100%;
    height: 500px;
    top:71px;
}

.imgsequence {
    display: flex;
    flex-direction: row;
    overflow: hidden;
    width: 100%;
    z-index: 1;
}

.imgsequence img {
    flex: 0 0 100%;
    width: 100%;
    height: 500px;
    object-fit: cover;
    z-index: 1;
}

#leftScroll {
    position: absolute;
    left: 0px;
    z-index: 10;
}

#rightScroll {
    position: absolute;
    right: 0px;
    z-index: 10;
}

#leftScroll, #rightScroll {
    top:225px;
    background-color:transparent;
    color: white;
    font-size: 50px;
    border:0;
    width: 70px;
    height: 70px;
}

.overlay {
    position: absolute;
    left: 0;
    bottom: 0;
    right: 0;
    height: 500px;
    z-index: 2;
    background: rgba(0,0,0,0.5);
}

.home-content {
    width: 100%;
    margin: 0 auto;
    position: absolute;
    top: 160px;
    color: #ffffff;
    z-index: 3;
}

.home-content h1 {
    text-align: center;
    text-transform: uppercase;
    font-size: 70px;
    line-height: 1.1;
}

#emptybox {
    height: 500px;
    width: 100%;
}

#projectSumTitle {
    position: absolute;
    top: 590px;
    width: 100%;
    text-align: center;
    font-size: 50px;
}

.projectSum {
    position: absolute;
    top: 700px;
    width: 100%;
    padding:40px 50px;
    display:grid;
    grid-template-columns: repeat(2, 1fr);
    grid-auto-rows: 700px;
    gap:20px;
}

@media screen and (max-width: 767px) {
    .home-content h1 {
        font-size: 35px;
        width: 350px;
        text-align: center;
    }
    .home-content {
        top: 80px;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .main {
        height: 300px;
        position:relative;
        top: 0;
        margin-bottom: 1200px;
    }
    #leftScroll, #rightScroll {
        top:110px;
        font-size: 30px;
        width: 50px;
        height: 50px;
    }
    .imgsequence img {
        height: 300px;
    }
    .overlay {
        height:300px;
    }
    div.blank {
        margin-bottom: 700px;
    }
    #projectSumTitle {
        top: 440px;
    }
    .projectSum {
        width: 100%;
        height: 100%;
        top: 550px;
        padding: 20px 50px;
        grid-template-columns: 1fr;
        grid-auto-rows: minmax(500px, auto);
    }
}